<script>
  import Cart from "./cart/Cart.svelte";
  import Tabs from "./tabs/Tabs.svelte";
  import ItemGrid from "./item/ItemGrid.svelte";

  import { fetchCard } from "../server";
  let card = "";

  function handleKeydown(key) {
    if (key.key === "Enter") {
      fetchCard(card);
      card = "";
    } else if (key.key.length === 1) card += key.key;
  }
</script>

